.calendar {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: auto;
  font-family: sans-serif;
}

.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.calendar-day {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin-right: 5px;
  margin-bottom: 5px;
  border-radius: 50%;
  background-color: #f2f2f2;
  color: #444;
}

.calendar-day:hover {
  background-color: #ddd;
}

.calendar-body {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
}
